.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 250px;
    background: #7aff9b;
    width: 800px;
    border-radius: 15px;

}

.quote{
    padding: 60px 110px;
    color: black;
    font-size: 32px;
    text-align: center;

}

.line{
    width: 680px;
    height: 1.5px;
    background: black;
}

.bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center ;
    margin: 20px 0px;
}

.author{
    color: black;
    font-size: 20px;
    font-weight: 500;

}

.icons{
    display: flex;
    gap: 40px;
}

.icons img{
    cursor: pointer;
    height: 36px;
}